import React,{useState, useEffect} from "react";
//import {Link} from "react-router-dom";
import sanityClient from "../client"


export default function Projects() {
    const[projectData, setProjectData]= useState(null);
    useEffect(()=> {
    sanityClient.fetch(`*[_type == "project"]{
        title,
        date,
        place,
        decription,
        projectType,
        language,
        link,
        tags

    }`).then((data) => setProjectData(data))
    .catch(console.error);
}, []);

return (
    <main className="bg-gray-100 min-h-screen p-12">
    <section className="container mx-auto">
          <h1 className="text-5xl flex justify-center cursive">My Project</h1>
          <br />
            <section className="grid md:grid-cols-2 lg:grid-col-3 gap-8">
                {projectData && projectData.map((project,index)=> (
                <article class="relative rounded-lg shadow-xl bg-white p-16">
                    <h3 className="text-gray-800 text-3xl font-bold mb-2 hover:text-pink-500 mx-3">
                        <a href={project.link} alt={project.title} target="_blank" rel="noopener noreferrer">{project.title}</a>
                    </h3>
                    <div className="text-gray-500 taxt-xs justify-between space-x-3">
                        <span className="mx-3 justify-between">
                            <strong className="font-bold">Finished on</strong>:{" "}
                            {new Date(project.date).toLocaleDateString()}
                        </span>
                        <span>
                        <strong className="font-bold">Location</strong>:{" "}
                        {project.place}
                        </span>
                        <span>
                        <strong className="font-bold">Project Type</strong>:{" "}
                        {project.projectType}
                        </span>
                        <p className="my-6 text-lg text-grat-700 leading-relaxed">
                            {project.decription}
                        </p>
                        <span>
                        <strong className="font-bold">Language</strong>:{" "}
                        {project.language}
                        </span>
                        <br />
                        <br />
                        <a href={project.link} rel="noopener noreferrer" target="_blank" class="text-black underline font-bold hover:underline hover:text-pink-500 text-xl">View The Project{" "}
                        <span role="img" aria-label="right pointer">
                        </span>
                        </a>
                        
                    </div>
                </article>
                ))}
            </section>
        </section>
    </main>
    
    )

}