import React from "react";
import {NavLink} from "react-router-dom";
import {SocialIcon} from "react-social-icons";


export default function Navbar() {
    return(
        <header className="bg-gray-700">
            <div className="container mx-auto flex justify-between">
                <nav className="flex">
                    <NavLink to="/" 
                    exact
                    activeClassName="text-white" 
                    className="inflex-flex item-center font-bold py-3 text-white hover:text-pink-500 text-4xl font-bold cursive tracking-widest">
                        Demi Taylor Nimmo 
                    </NavLink>
                </nav>  
                <nav className="flex">
                    <NavLink 
                    to="/"
                    activeClassName="text-white-100 font-bold"
                    className="inflex-flex item-center py-3 px-3 my-3 text-white hover:text-pink-500 ">
                        Home
                    </NavLink>
                    <NavLink 
                    to="/post"
                    activeClassName="text-white-100 font-bold"
                    className="inflex-flex item-center py-3 px-3 my-3 text-white hover:text-pink-500 ">
                        Blog Post
                    </NavLink>
                    <NavLink 
                    to="/projects"
                    activeClassName="text-white-100 font-bold "
                    className="inflex-flex item-center py-3 px-3 my-3 text-white hover:text-pink-500 ">
                        Projects
                    </NavLink>
                    <NavLink 
                    to="/about"
                    activeClassName="text-pink-400 font-bold"
                    className="inflex-flex item-center py-3 px-3 my-3 text-white hover:text-pink-500 ">
                        About
                    </NavLink>
                </nav>
                
                <div className="inline-flex py-3 px-3 my-2">
                    <SocialIcon 
                    url="https://twitter.com/_Demiinfinity" 
                    className="mr-4" 
                    target="_blank" 
                    fgColor="#fff" 
                    style={{height: 35, width: 35}}/>

                    <SocialIcon 
                    url="https://www.facebook.com/DemiTNimmo" 
                    className="mr-4" 
                    target="_blank" 
                    fgColor="#fff" 
                    style={{height: 35, width: 35}}/>
                    
                    <SocialIcon 
                    url="https://www.linkedin.com/in/demi-taylor-nimmo-bb320b40/" 
                    className="mr-4" 
                    target="_blank" 
                    fgColor="#fff" 
                    style={{height: 35, width: 35}}/>
                </div>

            </div>
        </header>
        
        )

}