import React from "react";
import image from "../coding-1841550_1920.jpg"
export default function Home() {
return(
  <main>
      <img src={image} 
      alt="Coding" 
      className="absolute object-cover w-full h-full"/>
      <section className="relative flex justify-center min-h-screen pt-12 lg:pt-32 px-8">
          <h1 className="text-6xl text-white font bold cursive leading-normal lg:leading-snug home-name">Welcome,</h1>
          
          <section className="relative justify-center min-h-screen pt-8 lg:pt-32 px-8">
                <p className="text-1xl text-white cursive leading-relaxed lg:leading-snug home-name"> I'm a Software Developer</p>
          </section>
      </section>
  </main>
  
    )

}